<template>
	<view class="page bgwhite">
		<view class="list">
			<u--form ref="uform" :model="form" :rules="rules" label-width="80px">
				<u-form-item :label="$t('工作中心')" style="padding-left: 10px;" prop="room">
					<view class="itemView">
						<u--input v-model="form.room" :placeholder="$t('please.select')" border="surround" suffixIcon="arrow-down"
							suffixIconStyle="color: #909399" style="height: 20px;" @focus="roomShow = true"></u--input>
						<u-picker :show="roomShow" :columns="columns" @cancel="roomShow=false" @confirm="roomConfirm"
							@change="roomChangeHandler" :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-picker>
					</view>

				</u-form-item>
				<u-form-item :label="$t('机器状态')" style="padding-left: 10px;" prop="status">
					<view class="itemView">
						<u--input v-model="form.status" :placeholder="$t('please.select')" border="surround" suffixIcon="arrow-down"
							suffixIconStyle="color: #909399" style="height: 20px;"
							@focus="statusShow = true"></u--input>
						<u-picker :show="statusShow" :columns="status" @cancel="statusShow=false"
							@confirm="statusConfirm" @change="statusChangeHandler" :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-picker>
					</view>

				</u-form-item>
				<u-form-item :label="$t('机器编号')" style="padding-left: 10px;" prop="machineNo">
					<u--input v-model="form.machineNo" :placeholder="$t('please.input')" border="surround"
						style="height: 20px;margin-right: 10px;"></u--input>
				</u-form-item>
			</u--form>
		</view>

		<u-button class="confirmBtn" shape="circle" :text="$t('screening')" @click="submit"></u-button>
	</view>
</template>

<script>
	import Api from '@/api/device/index.js'
	import pApi from "@/api/public/index.js"
	export default {
		data() {
			return {
				form: {
					room: null,
					wsCode: null,
					machineState: null,
					status: null,
					machineNo: null
				},
				rules: {},
				roomShow: false,
				statusShow: false,
				selectRoomIndex: 0,
				selectStatusIndex: 0,
				roomData: [],
				columns: [],
				statusData: [],
				status: []
			};
		},
		onLoad() {
			this.getRoomList()
			this.getMachineStatus()
		},
		onShow() {
			uni.setNavigationBarTitle({ // 修改头部标题
				title: this.$t("machine.search")
			});
		},
		methods: {
			//获取车间数据
			getRoomList() {
				uni.$u.http.get(Api.roomList).then(result => {
					console.log('result', result);
					if (result.code === 200) {
						this.roomData = result.data
						let roomList = []
						result.data.forEach(item => {
							roomList.push(item.name)
						})
						if (roomList.length > 0) {
							this.columns = []
							this.columns.push(roomList)
						}
					}
				}).catch(er => {
					console.log('er', er);
				})
			},

			//获取机器状态业务表
			getMachineStatus() {
				uni.$u.http.get(pApi.machineStatus).then(result => {
					console.log('result', result);
					if (result.code === 200 && result.data != null) {
						this.statusData = result.data
						let statusList = []
						result.data.forEach(item => {
							statusList.push(this.$t(item.name))
						})
						if (statusList.length > 0) {
							this.status = []
							this.status.push(statusList)
						}
					}
				}).catch(er => {
					console.log('er', er);
				})
			},

			submit() {
				this.$refs.uform.validate(valid => {
					if (valid) {
						console.log('验证通过');
					} else {
						console.log('验证失败');
					}
				});

				uni.$emit('returnMachineData', this.form)
				uni.navigateBack({
					delta: 1
				});
			},
			roomChangeHandler(e) {
				this.selectRoomIndex = e.index
			},
			statusChangeHandler(e) {
				this.selectStatusIndex = e.index
			},
			roomConfirm() {
				this.roomShow = false
				this.form.room = this.columns[0][this.selectRoomIndex]
				this.form.wsCode = this.roomData[this.selectRoomIndex].areaCode
			},
			statusConfirm() {
				this.statusShow = false
				this.form.machineState = this.statusData[this.selectStatusIndex].code
				this.form.status = this.status[0][this.selectStatusIndex]
			}
		}
	}
</script>

<style lang="scss" scoped>
	.list {
		&-item {
			padding: 10upx 16upx;
			@include after();
			font-size: 28upx;
			border: 0;
			justify-content: center;
			align-items: center;

			.label {
				width: 150upx;
				color: #333;
				font-weight: 500;
				margin-left: 10px;
			}

			.value {
				color: #666;
			}
		}
	}

	.label {
		width: 150upx;
		color: #333;
		font-weight: 500;
		margin-left: 10px;
	}

	.itemView {
		width: 100%;
		margin-right: 10px;
	}

	.confirmBtn {
		width: 80%;
		margin-top: 20px;
		margin-bottom: 20px;
		align-items: center;
		color: white;
		font-size: 24px;
		background-color: $ch-theme;
	}
</style>